<template>
  <div class="content">
    <h1>紫光集团重整方尘埃落定：阿里巴巴出局 智路建广联合体胜出</h1>
    <p>
      <img class="content-picture" src="~images/homeImg/swiperImg/2.jpg" />
    </p>
    <p>
      12月10日晚间，
      <span class="linkNormal">紫光国微</span>披露间接控股股东紫光集团重整进展公告称，确定北京智路资产管理有限公司和北京建广资产管理有限公司作为牵头方组成的联合体为紫光集团等七家企业实质合并重整战略投资者，依法与战略投资者推进重整投资协议签署及重整计划草案制定等相关工作。
    </p>
    <p>对于此次重整方的确定，中钢经济研究院首席研究员胡麒牧在接受《证券日报》记者采访时表示：“智路建广联合体在集成电路行业整合领域具有丰富的操作经验，也有丰富的行业资源。由此联合体来主导，可以将紫光集团在行业内的竞争优势通过重整发挥出来。对于紫光集团来讲，重整方的确定增强了市场对于重组成功的信心，有利于相关协议的尽快落地，对于企业重整后再出发有很好的推动作用。”</p>
    <p>
      紫光集团重整从今年7月份拉开帷幕。7月9日，紫光集团突发公告称，其债权人
      <span class="linkNormal">徽商银行</span>以紫光集团不能清偿到期债务，具备重整价值和重整可行性为由，向北京一中院申请对紫光集团进行破产重整。7月21日，紫光集团发布公告确定，本次引战为整体引战，战略投资者需整体承接紫光集团或紫光集团核心产业。
    </p>
    <p>随着此次重整方的“尘埃落定”，高禾投资管理合伙人刘盛宇在接受《证券日报》记者采访时表示，“在不到5个月的时间里，紫光集团确定重整人，这个进程已经比较快了。因为，第一要按照法律规定，进行相关程序的申报；第二，紫光集团整个资产体量和债务体量都非常庞大，要找到具备产业整合能力、战略投资能力，以及后续的资产经营能力的战略投资者其实是不太容易的。”</p>
    <p>“在紫光集团深陷债务危机的状态下，不管是花落谁家，重整方越早确定越有利于企业的重组整合，拖得时间越久，对于企业的稳定发展越不利。”创道投资咨询合伙人步日欣对《证券日报》记者表示。</p>
    <p>
      此前，
      <span class="linkNormal">阿里巴巴</span>与智路建广联合体一直是竞争对手，并且阿里巴巴旗下国内市场份额第一的阿里云，与紫光集团主营之一的云业务部分重合。但阿里巴巴集团在芯片领域投资的平头哥目前还处于起步阶段，只涉及芯片设计领域。对于此次阿里的出局，市场众说纷纭。步日欣表示，“真实原因很难断定，也可能是多因素叠加的结果，国际因素、地域因素以及阿里自身因素等，都有可能是促使阿里放弃的原因。”
    </p>
    <p>而打败阿里巴巴的智路建广联合体到底是何来头？</p>
    <p>公开资料显示，智路建广联合体是由智路资本（北京智路资产管理有限公司）和建广资产（北京建广资产管理有限公司）联合。建广资产成立于2014年，智路资本成立于2015年，两家机构的运作模式是将国外优质的资产收购，且多数是100%控股，收购标的涉及半导体ODM，IC设计、封装、材料、工业软件等，从2015年以来，智路资本和建广资产分别主导了多起金额过百亿元的半导体及硬科技领域的重大并购，其中包括安世半导体、安谱隆半导体、奥地利半导体等多起国内外重量级并购项目。</p>
    <p>据不完全统计，2015年至今，中国半导体大型并购合作超过10亿元人民币的项目近30个，建广资产和智路资本主导了超过一半的交易。</p>
    <p>“建广智路在半导体领域有丰富的经验，主导了很多大型半导体项目的海外收购和整合，业务能力、行业经验和资金实力能够匹配紫光的重整。”步日欣对记者说道。</p>
    <p>值得一提的是，重整计划草案根据相关法律规定须提交债权人会议表决通过，并经人民法院裁定批准后方可生效。刘盛宇表示，“随着紫光集团最终明确战略投资者，后续梳理债务资产以及确定重组方案应该会更快。”</p>
  </div>
